.App {
  margin: 1rem 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
}

.container {
  max-width: 75%;
}

.nav-container {
  width: 20rem;
  min-height: 95vh;
  padding: 20rem 1rem 0;
  position: relative;
  background: white;
  font-size: 2.25rem;
  border-right: 0.2rem solid rgba(0, 0, 0, 0.5);
}

.logo-container {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transform: translateY(-15rem);
      -ms-transform: translateY(-15rem);
          transform: translateY(-15rem);
}

.logo-container .logo {
  -webkit-transform: scale(0.5) rotate(-14deg);
      -ms-transform: scale(0.5) rotate(-14deg);
          transform: scale(0.5) rotate(-14deg);
}

.Navi {
  position: fixed;
  /* margin: 10rem auto; */
}

.Navi > * {
  width: 100% !important;
}

.menu-item {
  width: 100%;
}



